.movies__movie {
  display: flex;
  height: 100%;
  width: 95%;
  position: relative;
  padding: 10px;
  box-sizing: border-box;
}

.movies__poster {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-top: -10%;
}

.movies__movie img {
  height: 141.8px;
  width: 100px;
  margin-right: 20px;
  margin-bottom: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.movie__title {
  font-size: 22px;
  font-weight: 900;
  margin-bottom: 15px;
  height: 41px;
  line-height: 1.1em;
}
.movie__year {
  font-weight: 500;
  color: rgba(0, 0, 0, 0.7);
  font-size: 11px;
}

.movie__year::before {
  content: "<";
}
.movie__year::after {
  content: ">";
}

.movie__data {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.genres {
  display: flex;
}

.genres__genre {
  font-size: 9px;
  opacity: 0.6;
  margin-right: 10px;
  width: max-content;
  margin-bottom: 20px;
  font-weight: 700;
}

.movie__summary {
  font-size: 11px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;

  line-height: 20px;
  max-height: 100px;
  max-width: 300px;
  -webkit-line-clamp: 3;
  opacity: 0.8;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
