@import url(https://fonts.googleapis.com/css2?family=Ranchers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500;700;900&display=swap);
.movies__movie {
  display: flex;
  height: 100%;
  width: 95%;
  position: relative;
  padding: 10px;
  box-sizing: border-box;
}

.movies__poster {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-top: -10%;
}

.movies__movie img {
  height: 141.8px;
  width: 100px;
  margin-right: 20px;
  margin-bottom: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.movie__title {
  font-size: 22px;
  font-weight: 900;
  margin-bottom: 15px;
  height: 41px;
  line-height: 1.1em;
}
.movie__year {
  font-weight: 500;
  color: rgba(0, 0, 0, 0.7);
  font-size: 11px;
}

.movie__year::before {
  content: "<";
}
.movie__year::after {
  content: ">";
}

.movie__data {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.genres {
  display: flex;
}

.genres__genre {
  font-size: 9px;
  opacity: 0.6;
  margin-right: 10px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-bottom: 20px;
  font-weight: 700;
}

.movie__summary {
  font-size: 11px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;

  line-height: 20px;
  max-height: 100px;
  max-width: 300px;
  -webkit-line-clamp: 3;
  opacity: 0.8;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

input:focus {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  box-sizing: border-box;
  background-color: rgb(245, 251, 255);
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: "Roboto Slab", serif;
}
#root {
  display: flex;

  justify-content: center;
  align-items: center;
  height: inherit;
  width: 100%;
}

.container {
  display: flex;
  flex-direction: row;
  height: inherit;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  justify-content: center;
  margin-top: 200px;
}

.container .movies {
  display: grid;
  width: inherit;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  grid-auto-rows: 210px;
  grid-template-columns: repeat(2, 450px);

  grid-gap: 70px;

  gap: 70px;
  grid-row-gap: 100px;
  row-gap: 100px;
}
.loader {
  position: fixed;
  top: 40%;
  left: 40%;
}

.loader__text {
  font-size: 100px;
  font-family: "Ranchers", cursive;
}

.movies__movie {
  background-color: rgb(255, 255, 255, 0.9);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 20px;
}

